import React from 'react';
import DefaultLayout from '../layouts/DefaultLayout';
import styled from 'styled-components';

const AlertContainer = styled.div`
    display: inline-block;
    min-width: 400px;
    max-width: 800px;
    margin-top: 200px;
    color: var(--dark);
`;

const MainContainer = styled.div`
    position: absolute;
    margin-top: 56px;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: #eee;
`;

const Error404 = styled.h1`
    font-size: 5rem;
    font-weight: bold;
`;

export default () => {
    return (
        <DefaultLayout>
            <MainContainer className='text-center'>
                <AlertContainer>
                    <Error404>404</Error404>
                    <h1 className='text-center'>It seems you're lost! <i className='far fa-frown'></i></h1>
                </AlertContainer>
            </MainContainer>
        </DefaultLayout>
    );
};